




































































































export default {
	data() {
		return {
			ColorList: this.ColorList,
			shadow: false,
			bordersize: ''
		};
	},
	methods: {
		SetShadow(e) {
			this.shadow = e.detail.value
		},
		SetBorderSize(e) {
			this.bordersize = e.detail.value
		}
	}
}
